<script>
import appConfig from "@/app.config";
//import Layout from '../../layouts/auth'
//import Layout from '../../layouts/main'
//import PageHeader from '@/components/page-header'
import {ApiInv} from '../siscmpfc/inv/ApiInv.js'

export default {
   page: {
   title: "Site Infinity",
   meta: [{ name: "description", content: appConfig.description }]
  },
  //components: { Layout, PageHeader },
  //components: { Layout },
  //components: {},
  data() {
      return {
          title: "Site",
            items: [
                {
                text: "Dashboard",
                href: "/home",
                },
                {
                text: "Site",
                active: true,
                },
            ],
          imgUrl:'',
          //url:null,
          categorias:[],
          mostrarCategorias:false,
          api: new ApiInv,
          subcategoria : {id: 0, descricao: ""},
          produtos:{},
          currentPage:0,
          termo:'',
      }
  },
  created(){
      this.iniciar()
      this.listarProdutos()
  },
  mounted() {
      this.imgUrl=process.env.VUE_APP_IMG_URL
      //this.url=process.env.VUE_APP_SERVER_URL
      //this.listarProdutos()
  },
  methods:{
      ativarCategorias(){
          this.mostrarCategorias = !this.mostrarCategorias
      },
      async iniciar(){
          this.categorias = []
          //this.loading = true
          let result = await this.api.getCategorias()
          this.categorias = result
          let sub = await this.api.getSubCategorias()
          
          let aux = []
          this.categorias.forEach(categoria => {
              
              aux = []
              sub.forEach(subcategoria => {
                  if(subcategoria.categoria.id == categoria.id){
                      aux.push(subcategoria)
                  }
              });
              categoria.subcategoria = aux
              
          });
          //this.loading = false
          console.log("CATEGORIAS: =>", this.categorias)
        
      },

      showImageList(imagem){
            let result = ''
            if(imagem){
                if(imagem.startsWith('/media')){
                    result = this.imgUrl + imagem
                } else {
                    result = imagem
                }
            } else {
                result = this.imgUrl + 'media/img/empty.png'
            }
            return result
        },

      async listarProdutos(){
          this.imgUrl = ''
          this.produtos = []
          let produtos = await this.api.getProdutos()
          console.log(produtos)
          this.produtos = produtos
      },

      async listarProdutosByCategoria(id){
          this.imgUrl=process.env.VUE_APP_IMG_URL
          this.produtos = []
          console.log('id da categoria a ser buscada', id)
          let produtos = await this.api.getProdutos()
          console.log(produtos)
          this.produtos = produtos
          this.mostrarCategorias = false
      },

      async listarProdutosBySubCategoria(id){
          this.imgUrl=process.env.VUE_APP_IMG_URL
          this.produtos = []
          console.log('id da sub-categoria a ser buscada', id)
          let produtos = await this.api.getProdutoBySubcategoria(id)
          console.log(produtos)
          this.produtos = produtos
          console.log(this.produtos)
          this.mostrarCategorias = false
      },

      async listarProdutosByName(){
          let termo = this.termo
          this.imgUrl=process.env.VUE_APP_IMG_URL
          this.produtos = []
          let produtos = await this.api.getProdutoByName(termo)
          console.log(produtos)
          this.produtos = produtos
          console.log(this.produtos)
          this.mostrarCategorias = false
      },

      searchFilter(e) {
        const searchStr = e.target.value;
        let produtos = this.produtos
        this.produtos = produtos.filter((product) => {
            return (
            product.descricao.toLowerCase().search(searchStr.toLowerCase()) !== -1
            );
        });
      },
      irHome(){
          this.$router.push('/home')
      }
  }
};
</script>




